import React, { useState, useRef } from 'react'
import { Divider, Typography } from "@mui/material"
import TimelineBar from './TimelineBar';
import { data as peopleData } from './Data';
import { eventData as eventData } from './Data';
import Bars from './Bars';
import Time from './Time';




function findMinMaxYears(events) {
    if (!Array.isArray(events) || events.length === 0) {
        return null; // Return null for invalid input
    }

    let minStartYear = events[0].startYear;
    let maxEndYear = events[0].endYear;

    for (const event of events) {
        if (event.startYear < minStartYear) {
            minStartYear = event.startYear;
        }
        if (event.endYear > maxEndYear) {
            maxEndYear = event.endYear;
        }
    }

    return {
        smallestStartYear: minStartYear - minStartYear%50,
        largestEndYear: Math.ceil(maxEndYear / 100) * 100
    };
}


export default function Timeline(props) {
    const data = props.data
    data.sort((a, b) => a.startYear - b.startYear);
    const time = useRef()

    const subdivisions = [5, 10, 20, 25, 50, 100]
    const margins = { 0: 100, 1: 50, 2: 40, 3: 20, 4: 15, 5: 10, 6: 5, 7: 0 }
    const [numSubdivisions, setnumSubdivisions] = useState(0)
    const minMax = findMinMaxYears(data)
    const startYear = minMax.smallestStartYear
    const endYear = minMax.largestEndYear
    const interval = 100
    const intervalDistance = interval / subdivisions[numSubdivisions]


    const shortestDuration = 2 * intervalDistance / 40
    console.log(shortestDuration)
    for (let i = 0; i < data.length; i++) {
        let event = data[i]
        if ((event.endYear - event.startYear) < shortestDuration) {
            data[i].endYearFake = data[i].startYear + shortestDuration
        }
        else {
            data[i].endYearFake = data[i].endYear
        }
    }

    return (
        <div onDoubleClick={() => setnumSubdivisions(numSubdivisions == subdivisions.length - 1 ? 0 : numSubdivisions + 1)} style={{ display: "flex", width: "80vw", alignItems: "stretch", gap: 10 }}>
            <Time ref={time} margin={margins[numSubdivisions]} interval={100} startYear={startYear} endYear={endYear} numSubdivisions={subdivisions[numSubdivisions]} />
            <Bars data={data} margin={margins[numSubdivisions]} interval={100} startYear={startYear} endYear={endYear} numSubdivisions={subdivisions[numSubdivisions]} />
        </div>
    )
}



