import { Typography } from "@mui/material";
import React from 'react';

export default function Time(props) {
    const interval = props.interval; // largest tick marks
    const startYear = props.startYear;
    const endYear = props.endYear;
    const margin = props.margin
    const numSubdivisions = props.numSubdivisions;
    const markers = generateMarkers(interval, startYear, endYear, numSubdivisions)
    return (
        <div style={{ display: "flex", flexDirection: "column" }}>
            {markers.map((marker) => {
                return (
                    <div key={marker[0]} style={{ display: "flex", alignItems: "center", gap: 10, height: 24,marginBottom:margin }}>
                        <Typography color="initial" style={{ color: "gray" }}>
                            {marker[0]}
                        </Typography>
                        <hr style={{ margin: 8, width: Math.pow(1.65, (9 - marker[1])), borderColor: "gray", marginLeft: 0 }} />
                    </div>
                )
            })}

        </div>
    )
}

function pickBiggest(time) {
    const mapping = { 50: 2, 25: 3, 20: 4, 10: 5, 5: 6, 2: 7, 1: 8 }
    var length = 0

    for (let duration of [50, 25, 20, 10, 5, 2, 1]) {
        if (time % duration == 0) {
            length = mapping[duration]
            break
        }
    }
    return length
}



function generateMarkers(interval, startYear, endYear, numSubdivisions) {
    const markers = []
    const intervalDistance = interval / numSubdivisions
    for (let i = startYear; i <= endYear; i++) {
        if (i % interval == 0) {
            markers.push([i, 1])
        }
        else if (i % intervalDistance == 0) {
            const time = i % interval
            const length = pickBiggest(time)
            if (length != 0) {
                markers.push([i, length])
            }
        }
    }
    return markers
}

