import React, { useState } from 'react';
import { ToggleButton, ToggleButtonGroup } from '@mui/material';
import { Grid } from '@mui/material';
import { FormControl, InputLabel, MenuItem, Select, Chip } from '@mui/material';
import { data as peopleData } from './Data';
import { eventData as eventData } from './Data';

const ToggleOptions = ({ switchDataset,setDataset }) => {
    const [selectedOption, setSelectedOption] = useState('Events');

    const handleOptionChange = (event, newOption) => {
        if (newOption !== null) {
            setSelectedOption(newOption);
            switchDataset(newOption == "Events" ? eventData : peopleData)
            setDataset(newOption)
        }
    };

    return (
        <ToggleButtonGroup
            value={selectedOption}
            exclusive
            onChange={handleOptionChange}
            aria-label="toggle options"
        >
            <ToggleButton size="small" value="Events" aria-label="events">
                Events
            </ToggleButton>
            <ToggleButton size="small" value="Key Leaders" aria-label="key leaders">
                Key Leaders
            </ToggleButton>
        </ToggleButtonGroup>
    );
};

function filterTags(obj, selectedTags) {
    var tags = obj.tag
    if (typeof (tags) == "string") {
        tags = obj.tag.split(", ")
    }
    return tags.some(r => selectedTags.includes(r))

}
const MultiSelectCountries = ({ countries, filterData, dataset }) => {
    const [selectedCountries, setSelectedCountries] = React.useState([]);
    const data = dataset=="Events"?eventData:peopleData
    const handleCountryChange = (event) => {
        setSelectedCountries(event.target.value);
        const filtered = data.filter((obj) => filterTags(obj, event.target.value))
        if (filtered.length > 0) {
            filterData(filtered)
        }
    };

    return (
        <FormControl style={{ width: "400px" }}>
            <Select
                labelId="country-select-label"
                id="country-select"
                multiple
                value={selectedCountries}
                onChange={handleCountryChange}
            >
                {countries.map((country) => (
                    <MenuItem key={country} value={country}>
                        {country}
                    </MenuItem>
                ))}
            </Select>
        </FormControl>
    );
};


const CombinedComponent = ({ countries, filterData, dataset, switchDataset,setDataset }) => {
    return (
        <div style={{ display: "flex", justifyContent: "right", gap: 10, width: "90%" }}>
            <ToggleOptions setDataset={setDataset} switchDataset={switchDataset} />
            <MultiSelectCountries countries={countries} filterData={filterData} dataset={dataset} />
        </div>
    );
};

export default CombinedComponent;
