import React from 'react'
import Tooltip from '@mui/material/Tooltip'
import { useState } from 'react'

export default function TimelineBar(props) {
    const [tooltipsOpen, settooltipsOpen] = useState(true)
    const data = props.data
    const duration = data.endYearFake - data.startYear
    const intervalDistance = props.interval / props.numSubdivisions
    const height = (24+props.margin) / intervalDistance * duration
    const shift = (24+props.margin) / intervalDistance * ((data.startYear - props.timelineStartYear) - Math.abs(props.shiftCorrection))
    const overlap = props.overlap

    return (
        <Tooltip PopperProps={{
            modifiers: [
              {
                name: "offset",
                options: {
                  offset: [0, -10],
                },
              },
            ],
          }} open={tooltipsOpen} title={`${data.event}: ${data.startYear} - ${data.endYear}`}>
            <div style={{ borderRadius: 5, cursor:"pointer", backgroundColor: "#6f568e", opacity: 0.7, height: height, width: "20%", position: "relative", top: shift, left: `calc(calc(20% + 5px) * ${overlap})` }}>
            </div>
        </Tooltip>
    )
}
