import { default as React } from 'react';
import { data as allData } from './Data';
import TimelineBar from './TimelineBar';
import { useState } from 'react';




export default function Bars(props) {
    const data = props.data

    const interval = props.interval; // largest tick marks
    const startYear = props.startYear;
    const endYear = props.endYear;
    const numSubdivisions = props.numSubdivisions;
    const margin = props.margin

    const renderBars = () => {
        const overlaps = []
        return data.map((event, idx) => {
            const overlap = calculateOverlap(data.slice(0, idx + 1), idx, overlaps)
            const shiftCorrection = calculateShiftCorrection(data.slice(0, idx + 1), idx)

            return (
                <TimelineBar margin={margin} shiftCorrection={shiftCorrection} timelineStartYear={startYear} overlap={overlap} key={idx} data={event} interval={interval} numSubdivisions={numSubdivisions} />
            )

        })
    }

    return (
        <div style={{ flexGrow: 2, marginTop: 10 }}>
            {renderBars()}
        </div>
    )
}

function calculateShiftCorrection(data, indexOfEvent) {
    if (indexOfEvent < 0 || indexOfEvent >= data.length) {
        throw new Error("Invalid index. Please provide a valid index within the data range.");
    }

    let totalDuration = 0;

    // Iterate through events before the target event
    for (let i = 0; i < indexOfEvent; i++) {
        const { startYear, endYear, endYearFake } = data[i];


        const duration = endYearFake - startYear;
        totalDuration += duration;

    }

    return totalDuration;
}



function calculateOverlap(data, indexOfEvent, overlaps) {
    // Get the event at the specified index
    const targetEvent = data[indexOfEvent];
    var overlapNumbers = []
    // Filter the data to find events that overlap with the target event
    const overlappingEvents = data.filter((event, index) => {
        // Exclude the target event itself
        if (index === indexOfEvent) {
            return false;
        }
        // Check if the event overlaps with the target event
        const doesOverlap = event.startYear <= targetEvent.endYearFake && event.endYearFake >= targetEvent.startYear;
        if (doesOverlap) {
            overlapNumbers.push(overlaps[index])
        }
        return doesOverlap
    });
    overlapNumbers = overlapNumbers.sort()
    for (let i = 0; i < overlapNumbers.length; i++) {
        if (overlapNumbers[i] !== i) {
            overlaps.push(i)
            return i;
        }
    }
    // If no missing numbers, return the next number
    overlaps.push(overlapNumbers.length)
    return overlapNumbers.length;
}

